.placeholder {
  min-height: 120vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 5%;
  padding-right: 5%;
}

.placeholder .left {
  opacity: 0;
  transform: translateY(50px);
  transition: opacity 0.9s ease, transform 0.6s ease;
  display: flex;
  flex-direction: column;
  align-items: baseline;
  max-width: 45%;
}

.placeholder .left.fade-in {
  opacity: 1;
  transform: translateY(0);
}

.placeholder .left .title1 {
  font-size: 3.4rem;
  font-weight: 750;
}

.placeholder .left .title2 {
  font-size: 2.3rem;
  font-weight: 400;
}

.placeholder .left p {
  text-align: left;
  font-size: 1.5rem;
  font-weight: 400;
  margin-top: 2rem;
  margin-bottom: 0.5rem;
}

.placeholder .left button {
  border: none;
  width: 15rem;
  background-color: #f4d35e;
  color: black;
  padding-top: 1rem;
  padding-bottom: 1rem;
  font-size: 2rem;
  border-radius: 0.5rem;
  cursor: pointer;
}

.placeholder .left button:hover {
  background-color: #fff2c2;
  transition: all 0.5s;
}

.placeholder .left .actions {
  margin-top: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.placeholder .right {
  opacity: 0;
  transform: translateY(50px);
  transition: opacity 0.9s ease, transform 0.6s ease;
}

.placeholder .right img {
  height: 40rem;
}

.placeholder .right.fade-in {
  opacity: 1;
  transform: translateY(0);
}

@media screen and (max-width: 960px) {
  .placeholder {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-top: 10rem;
    min-height: 100vh;
  }

  .placeholder .right{
    margin-bottom: 1rem;
  }

  .placeholder .right img {
    height: 20rem;
  }

  .placeholder .left{
    max-width: 80%;
    padding: 0;
    margin: 0;
    text-align: left;
  }

  .placeholder .left .title1 {
    font-size: 2rem;
  }
  
  .placeholder .left .title2 {
    font-size: 1.5rem;
  }
  
  .placeholder .left p {
    font-size: 1.1rem;
  }
  
  .placeholder .left a {
    font-size: 1.1rem;
  }

  .placeholder .left button {
    width: 7rem;
    font-size: 1rem;
    padding-top: 0.7rem;
    padding-bottom: 0.7rem;
    -webkit-tap-highlight-color: transparent;
  }

  .placeholder .left button:hover {
    background-color: #f4d35e;
    transition: all 0.5s;
  }

  .placeholder .left button:active {
    background-color: #fff2c2;
    transition: all 0.5s;
  }

}
