.home {
  min-height: 100vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 5%;
  padding-right: 0;
}

.left {
  display: flex;
  flex-direction: column;
  align-items: baseline;
  max-width: 35%;
}

.left .title1 {
  font-size: 3.4rem;
  font-weight: 750;
  color: #15d6cd;
}

.left .title2 {
  font-size: 2.3rem;
  font-weight: 400;
  color: #ededed;
}

.left p {
  text-align: left;
  font-size: 1.5rem;
  font-weight: 400;
  color: #ededed;
}

.left button {
  border: none;
  width: 15rem;
  background-color: #f4d35e;
  color: black;
  padding-top: 1rem;
  padding-bottom: 1rem;
  font-size: 2rem;
  border-radius: 0.5rem;
  cursor: pointer;
}

.left button:hover {
  background-color: #fff2c2;
  transition: all 0.5s;
}

.left a {
  font-size: 1.7rem;
  color: #15d6cd;
}

.left .actions {
  margin-top: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.left img {
  height: 2rem;
  margin-left: 2rem;
  cursor: pointer;
}

.right img {
  height: 40rem;
}

.about_in_home{
    color: white;
    margin-bottom: -1rem;
    font-size: 1.5rem;
}

@media screen and (max-width: 960px) {
  .home {
    min-height: 80vh;
    max-height: 80vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 0;
    padding-right: 0;
    margin-bottom: -5rem;
  }

  .home .left {
    text-align: left;
    max-width: 80%;
    z-index: 99;
  }

  .home .left .title1 {
    font-size: 2rem;
  }

  .home .left .title2 {
    font-size: 1.5rem;
  }

  .home .left p {
    font-size: 1.1rem;
  }

  .home .left a {
    font-size: 1.1rem;
  }

  .home .left button {
    width: 7rem;
    font-size: 1rem;
    padding-top: 0.7rem;
    padding-bottom: 0.7rem;
    -webkit-tap-highlight-color: transparent;
  }

  .home .right{
    margin-top: -5rem;
  }

  .home .right img {
    height: 20rem;
  }

  .left button:hover {
    background-color: #f4d35e;
    transition: all 0.5s;
  }
  .left button:active {
    background-color: #fff2c2;
    transition: all 0.5s;
  }
}
