.about {
  min-height: 100vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 5%;
  padding-right: 0;
}

.about .title2 {
  font-size: 1.8rem;
  text-align: left;
  color: #15d6cd;
}

.about .left{
    margin-left: 5rem;
    margin-top: -3rem;
}

.about .left a {
  font-size: 1.4rem;
  font-weight: 300;
}

.about .left p {
  font-size: 1.2rem;
  font-weight: 200;
  line-height: 2rem;
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.about .actions{
    display: flex;
    justify-content: start;
}

.about .actions img{
    height: 2rem;
    margin-right: 1rem;
    margin-left: 0;
}

@media screen and (max-width: 960px) {
    .about {
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding-left: 0;
      padding-right: 0;
    }
  
    .about .left{
        min-width: 80%;
        margin-left: 0rem;
        margin-top: 3rem;
        z-index: 99;
    }
  
    .about .left .title1 {
      font-size: 2rem;
    }
  
    .about .left .title2 {
      font-size: 1.5rem;
    }
  
    .about .left p {
      font-size: 1.1rem;
    }
  
    .about .left a {
      font-size: 1.1rem;
    }
  
    .about .left button {
      width: 7rem;
      font-size: 1rem;
      padding-top: 0.7rem;
      padding-bottom: 0.7rem;
      -webkit-tap-highlight-color: transparent;
    }
  
    .about .right{
      margin-top: -5rem;
    }
  
    .about .right img {
      height: 20rem;
    }
  }
  